                    </el-tab-pane>
                </el-tabs>
                <p class="positions position_color" @click="centerDialog = true">
                    <<购卡请联系客服人员>>
                </p>
            </el-dialog>
        </div>
        <div>
            <el-row class="smallimg">
                <img src="../../assets/images/login/logo.png" />
                <el-row class="logoimg">
                    <img src="../../assets/images/login/logoimg.png" />
                </el-row>
            </el-row>
            <el-form ref="login" :rules="rules" :model="form" action="">
                <el-form-item prop="username">
                    <el-input class="el-inputs" v-model="form.username" placeholder="请输入手机号码"></el-input>
                    <img src="../../assets/images/login/username.png" />
                </el-form-item>
                <el-form-item prop="userpassword">
                    <el-input type="password" v-model="form.userpassword" placeholder="请输入密码"></el-input>
                    <img src="../../assets/images/login/password.png" />
                </el-form-item>
                <el-form-item>
                    <el-radio-group v-model="form.resource">
                        <el-radio label="记住密码"></el-radio>
                    </el-radio-group>
