                </el-form-item>
                <div class="footers">
                    <span @click="centerDialogVisible = true">忘记密码</span><span>|</span><span @click="dialogVisible = true">注册账号</span>
                </div>
            </el-form>
        </div>
        <div class="register">
            <el-dialog :visible.sync="dialogVisible" width="500px" :close-on-click-modal="false">
                <el-form ref="register" :rules="rules" :model="form" action="">
                    <el-form-item prop="username">
                        <el-input class="el-inputs" v-model="form.username" placeholder="请输入手机号码"></el-input>
                        <img src="../../assets/images/login/username.png" />
                    </el-form-item>
                    <el-form-item prop="usercode">
                        <el-input type="usercode" v-model="form.usercode" placeholder="请输入验证码"></el-input>
                        <span class="getCode" @click="getCode">获取验证码</span>
                    </el-form-item>
                    <el-form-item prop="userpassword">
                        <el-input type="password" v-model="form.userpassword" placeholder="请输入密码"></el-input>
                        <img src="../../assets/images/login/password.png" />
                    </el-form-item>
                    <el-form-item prop="password1">
                        <el-input type="password" v-model="form.password1" placeholder="请再次输入密码"></el-input>
                        <img src="../../assets/images/login/password.png" />
                    </el-form-item>
                    <el-form-item prop="usercard">
                        <el-input type="usercard" v-model="form.usercard" placeholder="请输入卡号"></el-input>
                        <img src="../../assets/images/login/card.png" />
                    </el-form-item>
                    <el-form-item>
                        <span class="register_footer">
                            <el-button type="primary" @click="registerform">注册</el-button>
                            <el-button type="primary" @click="">充值</el-button>
                            <span @click="">注册教程</span>
                            <!-- <p class="buyCard" @click="centerDialog = true">注：如无卡号请联系客服购买卡号 >></p> -->
                        </span>
                    </el-form-item>
                </el-form>
            </el-dialog>
        </div>
        <div class="buy_card">
            <el-dialog :visible.sync="centerDialog" width="362px" center :close-on-click-modal="false">
                <el-row class="titles">
                    <img src="../../assets/images/login/borders.png" />
                    <span>咨询热线</span>
                    <p>有问题，我来帮您解决</p>
                </el-row>
                <p class="bottoms"></p>
                <el-row class="box_phone">
                    <span class="phone">400-029-7500</span>
                    <p class="bottoms2"></p>
                    <span class="phone">400-029-7500</span>
                </el-row>
            </el-dialog>
        </div>
        <div class="forget_password">
            <el-dialog :visible.sync="centerDialogVisible" width="500px" center :close-on-click-modal="false">
                <el-form ref="forget_password" class="marginTop" :rules="rules" :model="form" action="">
                    <el-form-item prop="username">
                        <el-input class="el-inputs" v-model="form.username" placeholder="请输入手机号码"></el-input>
                        <img src="../../assets/images/login/username.png" />
                    </el-form-item>
                    <el-form-item prop="usercode">
                        <el-input type="usercode" v-model="form.usercode" placeholder="请输入验证码"></el-input>
                        <span class="getCode" @click="forgetCode">获取验证码</span>
                    </el-form-item>
                    <el-form-item prop="userpassword">
                        <el-input type="password" v-model="form.userpassword" placeholder="请输入新密码"></el-input>
                        <img src="../../assets/images/login/password.png" />
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input type="password" v-model="form.password" placeholder="请再次输入密码"></el-input>
                        <img src="../../assets/images/login/password.png" />
                    </el-form-item>
                    <el-form-item>
                        <span class="register_footer">
                            <el-button type="primary" @click="editPassword('from')">修改密码</el-button>
                        </span>
                    </el-form-item>
                </el-form>

            </el-dialog>
        </div>
    </div>
</template>
<script>
    export default {
         data() {
            // 自定义一个手机号验证规则
            var checkMoblie = (rule, value, callback) => {
                if (!value.trim()) {
                    return callback(new Error('请输入手机号'))
                }
                if (!/^1[34578]\d{9}$/.test(value)) {
                    return callback(new Error('手机号有误！请重填'))
                } else {
                    callback()
                }
            }
            // 验证两次密码一致
            var validatePass1 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.form.password !== '') {
                        this.$refs.register.validateField('password1');
                    }
                    callback();
                }
              };
                var validatePass = (rule, value, callback) => {
                    if (value === '') {
                        callback(new Error('请输入密码'));
                    } else {
                        if (this.form.password !== '') {
                            this.$refs.forget_password.validateField('password');
                        }
                        callback();
                    }
                  };
                var validatePass2 = (rule, value, callback) => {
                    if (value === '') {
                        callback(new Error('请再次输入密码'));
                    } else if (value !== this.form.userpassword) {
                        callback(new Error('两次输入密码不一致!'));
                    } else {
                        callback();
                    }
                };
            return {
                dialogVisible: false,
                form: {
                    username: '',
                    userpassword: '',
                    usercard: '',
                    usercode: '',
                    password: '',
                    password1: ''

                },
                activeName: 'second',
                loading: false,
                centerDialogVisible: false,
                centerDialog: false,
                centerVisible: false,
                rules: {
                    username: [{
                            required: true,
                            message: '请输入手机号码',
                            trigger: 'blur'
                        },
                        {
                            validator: checkMoblie,
                            trigger: 'blur'
                        }
                    ],
                    usercode: [{
                            required: true,
                            message: '请输入正确的验证码',
                            trigger: 'blur'
                        },
                        {
                            min: 6,
                            max: 6,
                            message: '验证码长度是6位数字',
                            trigger: 'blur'
                        }
                    ],
                    userpassword: [{
                            required: true,
                            validator: validatePass,
                            trigger: 'blur'
                        },
                        {
                            pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/,
                            message: '密码为8到16位的数字和字母组成'
                        }
                    ],
                    password: [{
                        required: true,
                        validator: validatePass2,
                        trigger: 'blur'

                    },
                     {
                         pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/,
                         message: '密码为8到16位的数字和字母组成'
                     }
                    ],
                    password1: [{
                        required: true,
                        validator: validatePass2,
                        trigger: 'blur'

                    },
                     {
                         pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/,
                         message: '密码为8到16位的数字和字母组成'
                     }
                    ]
                    // usercard: [{
                    //         required: true,
                    //         message: '请输入卡号',
                    //         trigger: 'blur'
                    //     }

                    // ]
                }
            }
        },
        methods: {
            handleClick(tab, event) {
                console.log(tab, event);
            },
            // 注册验证码
            getCode() {
                this.axios.post(process.env.API_HOST + '/api/sms/send', {
                    mobile: this.form.username,
                    event: 1,
                }).then((data) => {
                        let e = JSON.parse(data.data)
                        if (e.status == 'ok') {
                            this.$message({
                                message: '验证码发送成功',
                                center: true,
                                type: 'success',
                                duration:1000,
                            });
                        } else {
                            this.$alert(e.msg, '错误提示', {
                                confirmButtonText: '确定'
                            })
                        }
                    }).catch((error) => {
                        console.log(error)
                    })
            },
            // 注册
            registerform() {
                this.$refs.register.validate((status) => {
                    if (status) {
                        this.axios.post(process.env.API_HOST + '/api/user/register', {
                            mobile: this.form.username,
                            password: this.form.userpassword,
                            varcode: this.form.usercode,
                            //card_number: this.form.usercard,
                        })
                            .then((data) => {
                                console.log(data)
                                let e = JSON.parse(data.data)
                                if (e.status == 'ok') {
                                    customClass: 'Boxmessage'
                                    const weiXinUrl = require("../../assets/images/login/sussecd.png");
                                    this.$message({
                                        dangerouslyUseHTMLString: true,
                                        message: "<img id='success' src=" + weiXinUrl + ">",
                                        duration: 2000
                                    });
                                    setTimeout(function(){
                                        location.reload()
                                    },3000)
                                } else {
                                    this.$alert(e.msg, '错误提示', {
                                        confirmButtonText: '确定'
                                    })
                                }
                            }).catch((error) => {
                                console.log(error)
                            })
                        // this.loading = true
                    } else {
                        console.log(status)
                    }
                })
            },
            // 登录
            loginform() {
                this.$refs.login.validate((status) => {
                    if (status) {
                        this.axios.post(process.env.API_HOST + '/api/user/login',
                            {
                                mobile: this.form.username,
                                password: this.form.userpassword,
                                card_number:this.form.usercard
                            }
                        )
                            .then((data) => {
                                let e = JSON.parse(data.data)
                                if (e.status == 'ok') {
                                    sessionStorage.setItem("login_token", e.data.token);
                                    sessionStorage.setItem("userInfo", e.data.username);
                                    sessionStorage.setItem("usercard", e.data.card_number);
                                    this.$message({
                                        message: '登录成功',
                                        center: true,
                                        type: 'success',
                                        duration:2000
                                    });
                                    this.$router.push('/')
                                } else {
                                    this.$alert(e.msg, '错误提示', {
                                        confirmButtonText: '确定'
                                    })
                                }
                            }).catch((error) => {
                                console.log(error)
                            })

                    } else {
                        console.log(status)
                    }
                })
            },
            // 忘记密码验证码
            forgetCode() {
                this.axios.post(process.env.API_HOST + '/api/sms/send', {
                    mobile: this.form.username,
                    event: 2
                })
                    .then((data) => {
                        let e = JSON.parse(data.data)
                        if (e.status == 'ok') {
                            this.$message({
                                message: '验证码发送成功',
                                center: true,
                                type: 'success',
                                duration:1000
                            });
                        } else {
                            this.$alert(e.msg, '错误提示', {
                                confirmButtonText: '确定'
                            })
                        }
                    }).catch((error) => {
                        console.log(error)
                    })
            },
            // 忘记密码
            editPassword(forget_password) {
                this.$refs.forget_password.validate((valid) => {
                    if (valid) {
                        this.axios.post(process.env.API_HOST + '/api/user/forget_pass', {
                            mobile: this.form.username,
                            password: this.form.userpassword,
                            varcode: this.form.usercode
                        })
                            .then((data) => {
                                let e = JSON.parse(data.data)
                                if (e.status == 'ok') {
                                    const weiXinUrl = require("../../assets/images/login/pass.png");
                                    this.$message({
                                        dangerouslyUseHTMLString: true,
                                        message: "<img id='success' src=" + weiXinUrl + ">",
                                        duration: 2000
                                    });
                                    setTimeout(function(){
                                        location.reload()
                                    },3000)
                                } else {
                                    this.$alert(e.msg, '错误提示', {
                                        confirmButtonText: '确定'
                                    })
                                }
                            }).catch((error) => {
                                console.log(error)
                            })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });

            }
        }
    }
</script>
<style src="../../assets/css/login.css" scoped>
</style>
<style>
    /* 注册成功图片 */
    .el-message {
        border-style: unset;
        background-color: unset;
        padding: 0;
        top: 274px!important;
    }
    .el-message__icon {
        display: none;
    }
</style>



