<template>
    <div class="">
        <div class="main_img">
            <a>主图设置</a>
            <select value="第二张做主图" class="basics_option">
                <option>第二张做主图</option>
                <option>第一张做主图</option>
            </select>
            <el-checkbox class="left">删除第一张主图</el-checkbox>
            <a class="basics_margin">详情图设置,只下载前<input class="basics_input"/>张图</a>
            <el-checkbox class="left">详情随机排序</el-checkbox>
            <el-checkbox>过滤主图数量小于
                <input class="basics_input"/>
            </el-checkbox>
        </div>

        <div class="middle">
            <el-checkbox class="middle_detail">详情不添加水印</el-checkbox>
            <el-checkbox class="middle_set">图片处理使用图片七十二变的设置进行处理，由于主图/颜色图的特殊性，不会进行分割操作</el-checkbox>
        </div>

        <div class="bottom">
            <el-checkbox>补充图片,主图补充到<input class="input_width"/>张，原则
                <select value="不补充" class="bottom_option">
                    <option>不补充</option>
                    <option>补充</option>
                </select>
                <a>,详情图补充到<input class="input_width"/>张，原则
                    <select value="增加顺序(主图→颜色图)" class="bottom_option2">
                        <option>增加顺序(主图→颜色图)</option>
                    </select>
                </a>
            </el-checkbox>
        </div>
    </div>
</template>

<script>
    export default {
        name: "imgset",
        data() {
            return {

            }
        }
    }
</script>
<style scoped>
    .main_img, .middle, .bottom {
        margin-left: 5px;
    }
    .main_img .basics_option {
        width: 104px;
        height: 25px;
    }
    .main_img .left {
        margin-left: 20px;
    }
    .main_img .basics_input, .bottom .input_width {
        width: 40px;
        height: 24px;
        border: 1px solid #ccc;
    }
    .main_img .basics_input:nth-child(1), .bottom .input_width {
        margin: 0 5px;
    }
    .middle, .bottom {
        margin-top: 10px;
    }
    .middle .middle_set {
        color:rgba(254,81,2,1);
    }
    .bottom .bottom_option, .bottom .bottom_option2 {
        width: 170px;
        height: 25px;
    }
</style>



