    .concheck22 .left_input >>> .el-input__inner {
        width: 52px;
        height: 22px;
        padding: unset;
        text-align: center;
    }
    .concheck11 {
        width: 238px;
    }
    .concheck11 .left_input2 >>> .el-input__inner, .concheck11 .left_input2 {
        width: 33px;
    }
    .concheck11 .left_input3 >>> .el-input__inner, .concheck11 .left_input3 {
        width: 40px;
    }
    .stacaiji {
        width: 85px;
    }
    .jzcaijibox .concheck22, .jzcaijibox .concheck33 {
        border-color: #ccc;
    }
    .jzcaijibox .concheck11, .jzcaijibox .concheck22 {
        border-right: none;
    }
    .jzcaijibox .concheck11 {
        border-left: 1px solid #ccc;
    }
    /* 批量采集 */
    .plcaiji ol li{
        width: 46px;
    }
    .plcaiji ol li:nth-child(3) {
        width: 220px;
    }
    .plcaiji ol li:nth-child(4), .plcaiji ol li:nth-child(5) {
        width: 85px;
    }
    .pinduoduo, .taobao, .tianmao, .yiliubaba {
        cursor: pointer;
    }
    .pinduoduo {
        background-color: rgba(252,87,35,.2);
    }
    .showtaobao, .showtianmao, .showyiliubaba {
        display: none;
    }
    .plgjc, .plinput, .zdbccheck >>> .el-checkbox {
        font-size:13px;
        font-family:SimHei;
        font-weight:400;
        color:rgba(95,95,95,1);
    }
    .plgjc a {
        font-size:12px;
        color:rgba(253,81,1,1);
    }
    .plqunzi {
        border-bottom: 1px solid #ccc;
        border-top: 1px solid #ccc;
        width: 215px;
        height: 129px;
        overflow-y: auto;
        padding: 5px 0;
    }
    .inputnum {
        width: 54px;
    }
    .plinput {
        color:rgba(50,50,50,1);
    }
    .startcaiji {
        display: inline-block;
        margin-top: 15px;
        margin-right: 10px;
    }
    .zdbccheck >>> .el-checkbox {
        margin-top: 10px;
        color:rgba(50,50,50,1);
        margin-right: 15px;
    }
    .apitype {
        margin-top: 15px;
    }
    .plinput .dialog1_user {
        bottom: 40px;
    }
    /* 同行采集 */
    .thcaiji ol li {
        width: 90px;
    }
    .thcaiji ol li a, .starbottom, .tkimg a {
        font-size:12px;
        color:rgba(253,81,1,1);
    }
    .thcaiji ol li:nth-child(3) {
        width: 109px;
    }
    .thcaiji ol li:last-child {
        width: unset;
    }
    .thqunzi {
        border-top: none;
    }
    .starfooter .starts {
        margin-top: 81px;
    }
    .starfooter .dialog1_user {
        bottom: 42px;
    }
    .fzcolor, .tkimg, .tkfooter, .zdfooter {
        font-size:13px;
        font-family:SimHei;
        font-weight:400;
        color:rgba(50,50,50,1);
    }
    .starbottom {
        margin-top: 10px;
    }
    /* 找淘宝同款 */
    .tktaobao .tkol li:nth-child(2), .tktaobao ol li:nth-child(2) {
        width: 273px;
    }
    .tktaobao .tkol li:last-child {
        width: 339px;
        border-right: 1px solid #ccc;
    }
    .tkol {
        width: 692px;
    }
    .tkinput {
        margin-top: 10px;
        width: 205px;
        padding-left: 10px;
    }
    .tkfooter .starts, .tkfooter .el-checkbox {
        margin-top: 20px;
    }
    .tktaobao {
        position: relative;
    }
    .tkposition {
        position: absolute;
        right: 0;
        top: 10px;
    }
    .tktaobao .dialog1_user {
        right: 3px;
        bottom: 8px;
    }
    /* 整店采集 */
    .zdcaiji .zdol li:nth-child(3), .zdchildol ol li:nth-child(3) {
        width: 200px;
    }
    .zdcaiji .zdol li:nth-child(4), .zdcaiji .zdol li:nth-child(5), .zdchildol ol li:nth-child(4), .zdchildol ol li:nth-child(5) {
        width: 95px;
    }
    .zdcaiji .zdol li:last-child {
        width: 217px;
        background:rgba(252,88,36,.2);
    }
    .shopcheck .el-checkbox >>> .el-checkbox__label, .shopcheck a {
        font-size:13px;
        font-family:SimHei;
        font-weight:400;
        color:rgba(50,50,50,1);
        margin-top: 10px;
    }
    .shopcheck .el-checkbox {
        margin-right: 0;
    }
    .shopcheck a {
        font-size: 12px;
    }
    .shopcheck >>> .el-checkbox__label {
        padding-left: 3px;
    }
    .zdfooter .starts {
        margin-top: 10px;
    }
</style>



