<template>
    <div class="Problembox">
        <el-row :gutter="6">
            <el-col :span="2"><div class="Problem" @click="dialogVisible = true">问题反馈</div></el-col>
            <el-col :span="2" :offset="19" class="chat_assistant"><div class="assistant">打开聊天小助手</div></el-col>
        </el-row>
        <el-dialog
          title="问题反馈"
          :visible.sync="dialogVisible"
          width="1000px"
          :modal="false">
            <span class="pro_icon"><img src="../assets/images/1.png" /></span>
            <div class="pro_text">
                <h3>问题描述：</h3>
                <el-input type="textarea"></el-input>
            </div>
            <div class="pro_upload">
                <h3>问题截图：</h3>
                <el-upload
                  class="avatar-uploader"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                  :before-upload="beforeAvatarUpload">
                  <img v-if="imageUrl" :src="imageUrl" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </div>
            <div class="btn"><el-button>提交</el-button></div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "Problem",
        data() {
            return {
                dialogVisible: false,
                imageUrl: '',
            }
        },
        methods:{
            handleAvatarSuccess(res, file) {
                this.imageUrl = process.env.API_HOS + URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            }
        }
    }
</script>
<style scoped>
    /deep/ .el-row{
        height: 50px!important;
        padding-top: 13px;
    }
    .Problem{
        width:120px;
    }
    .assistant, .Problem {
        height:25px;
        line-height: 25px;
        text-align: center;
        border:1px solid rgba(11,66,201,.5);
        border-radius:5px;
        font-size:14px;
        font-weight:400;
        color:rgba(116,116,116,1);
        cursor: pointer;
    }
    .assistant{
        width:125px;
    }
    .el-checkbox-group >>>.el-input__inner{
        width: 32px;
        height: 18px;
    }
    .Problembox >>> .el-dialog {
        margin-top: 0vh!important;
        height: 680px;
        margin: 0 auto;
    }
    .Problembox >>> .el-dialog__header {
        padding: 10px 28px 10px;
        border-bottom: 1px solid #ccc;
    }
    .Problembox >>> .el-dialog__headerbtn {
        top: 2px;
        right: 6px;
        font-size: 26px;
    }
    .pro_icon {
        position: absolute;
        top: 15px;
        left: 11px;
    }
    .pro_text h3, .pro_upload h3{
        width:85px;
        height:16px;
        font-size:16px;
        font-family:SimHei;
        font-weight:400;
        color:rgba(50,50,50,1);
        display: inline-block;
    }
    .el-textarea {
        width: 835px;
        margin-top: -10px;
        vertical-align: top;
    }
    /deep/ .el-textarea__inner {
        height: 235px;
    }
    .pro_upload {
        margin-top: 50px;
    }
    /* .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    } */
    .avatar-uploader {
        width: 206px;
        height: 235px;
    }
    .avatar-uploader img {
        width: 100%;
        height: 100%;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader {
        display: inline-block;
    }
    .avatar-uploader-icon {
        border: 1px solid #ccc;
        font-size: 38px;
        color: #8c939d;
        width: 206px;
        height: 235px;
        line-height: 235px;
        text-align: center;
        vertical-align: top;
        margin-top: -20px;
    }
    /* /deep/ .el-upload--picture-card  {
        width: 206px;
        height: 235px;
        display: block;
        border: 1px solid #ccc;
        vertical-align: top;
    }*/
    .pro_upload >>> .el-upload--picture-card i {
        font-size: 50px;
        line-height: 4.5;
    }
    .el-button {
        width: 90px;
        background-color: rgba(21,39,220,.8);
        color: #fff;
        font-size: 18px;
        line-height: 13px;
    }
    .btn {
        margin-top: 20px;
        text-align: center;
    }
</style>



