                                            <div class="content_footer">查看已上传</div>
                                        </div> -->
                                    </ul>
                                </el-tab-pane>
                                <el-tab-pane label="百宝箱" name="three">
                                    <chest></chest>
                                </el-tab-pane>
                            </el-tabs>
                        </ul>
                    </el-tab-pane>
                    <el-tab-pane label="店铺管理">
                        <shopAdmin v-bind:content="shopData"></shopAdmin>
                    </el-tab-pane>
                    <el-tab-pane label="更新日志">
                        <Updatelog></Updatelog>
                    </el-tab-pane>
                </el-tabs>
                <div class="advertisement">
                    <span>广告位</span>
                </div>
            </el-main>
            <el-footer>
                <Problem></Problem>
            </el-footer>
        </div>
    </div>
</template>
<script>
import $ from 'jquery';
import Sjgl from "../../components/sjgl";
import imgset from "../../components/imgset";
import pddset from "../../components/pddset";
import shopAdmin from "../../components/shopAdmin";
import Updatelog from "../../components/Updatelog";
import Problem from "../../components/Problem";
import dataFilter from "../../components/dataFilter";
import collection from "../../components/collection";
import laboratory from "../../components/laboratory";
import chest from "../../components/chest";
import shoptable from "../../components/shoptable";
export default {
    data() {
        return {
            websitenames: [],
            tzaddr: '',
            webaddr: '',
            tabPosition: 'left',
            activeName: 'first',
            active: '1',
            input: '',
            input2: '$$',
            text: '',
            text2: '',
            text3: '',
            text4: '',
            text5: '',
            text6: '',
            text7: '',
            checked: true,
            desc1: '',
            desc2: '',
            formInline: {
                region: '',
                type:'',
                name: ''
            },
            keyword: '',
            start_price: '',
            end_price: '',
            tableData: [],
            shopData: [],
            show: true,
            checkList: ['选中且禁用','复选框 A'],
            userinfo: sessionStorage.getItem("userInfo"),
            times:'',
            shopLoginUrl: '',
            downloadpath: '',
            owner_name: '',
            template_id: '',
            template: '',
            url: process.env.API_HOST + '/api/collection/collection_download?pathfile=',
            Alibaba: [],
            loading: false
        };
    },
    components: {
        Sjgl,
        imgset,
        pddset,
        shopAdmin,
        Updatelog,
        Problem,
        dataFilter,
        collection,
        laboratory,
        chest,
        shoptable
    },
    mounted() {
        // this.toggle();
        this.websitename()
        this.getShopData()
        this.getShopName()
    },
    methods:{
        // 网址名
        websitename:function () {
            this.axios.post(process.env.API_HOST + '/api/index/weblist', {
                token: sessionStorage.getItem("login_token")
            }).then((data) => {
                let e = JSON.parse(data.data)
                this.websitenames = e.data
                this.formInline.region = e.data[0].title
                this.formInline.type = e.data[0].weburl
                this.formInline.name = e.data[0].name
            })
        },
        // 平台店铺登录接口
        getShopData: function () {
            this.axios.post(process.env.API_HOST + '/api/index/pshoplogin', {
                token: sessionStorage.getItem("login_token")
            }).then((data) => {

                let e = JSON.parse(data.data)

                if (e.status == 'ok') {
                    this.shopData = e.data

                }
            })
        },
        shopLogin: function (url) {
            this.shopLoginUrl = url
        },
        // 基础操作上传
        proUpload: function () {
            console.log(this.text.split('//'))
            let arr = [];
            let ids = [];
            arr = this.text.split('//');
            arr.shift()
            let axios = this.axios
            $.each(arr, function (index, val) {
                let num = val.indexOf('=') + 1;
                // ids.push(val.substr(num))
                let goodsIds = val.substr(num).replace(/[\r\n]/g,"")
                axios.post(process.env.API_HOST + '/api/collection/one_details', {
                    token: sessionStorage.getItem("login_token"),
                    goodsids: goodsIds
                }).then((data) => {
                    console.log(data)
                })
            })

        },
        //网址
        selected:function(){
            this.formInline.type = this.websitenames[this.formInline.region].weburl
            this.formInline.name = this.websitenames[this.formInline.region].name
            this.formInline.region = this.websitenames[this.formInline.region].title
        },
        // 采集
        caiji:function(){
            this.loading = true;
            var type = this.formInline.name
            if (this.keyword == '') {
                return
            }
            this.axios.post(process.env.API_HOST + '/api/collection/goodslist', {
                token: sessionStorage.getItem("login_token"),
                keyword: this.keyword,
                start_price: this.start_price,
                end_price: this.end_price,
                type: type,
                page: this.input
            }).then((data) => {
                let e = JSON.parse(data.data)
                console.log(e)
                this.downloadpath = e.data.path
                if (e.status == 'ok') {
                    this.tableData = e.data.data
                    $(".main_footer .el-table").css('display', 'block');
                }
                this.loading = false;
            })
        },
        // 拼多多数据上传
        appendData:function (res) {
            this.template_id = res
        },
        // 拼多多数据上传
        templateId:function (res) {
            this.owner_name = res
        },
         // 拼多多数据上传
        pddgoodsadd: function () {
          if (this.owner_name == '') {
              this.$alert('请选择店铺', '错误提示', {
                  confirmButtonText: '确定'
              })
              return;
          }
          if (this.template_id == '') {
              this.$alert('请选择运费模板', '错误提示', {
                  confirmButtonText: '确定'
              })
              return;
          }
          let goodsIds = this.getGoodsId(this.text4);
          if (goodsIds == '') {
              this.$alert('请输入商品链接', '错误提示', {
                  confirmButtonText: '确定'
              })
              return;
          }
          let axios = this.axios;
          //console.log(this.owner_name, this.template_id);
          axios.post(process.env.API_HOST + '/api/pnduoduo/pddgoodsadd', {
              token: sessionStorage.getItem("login_token"),
              goodsids: goodsIds,
              owner_name: this.owner_name,
              template_id: this.template_id
          }).then((data) => {
              console.log(data)
          })
        },
        // 拼多多获取详情
        pddupload:function(){
            let goodsIds = this.getGoodsId(this.text4);
            if (goodsIds == '') {
                this.$alert('请输入商品链接', '错误提示', {
                    confirmButtonText: '确定'
                })
                return;
            }
            let axios = this.axios;
            axios.post(process.env.API_HOST + '/api/collection/one_details', {
                token: sessionStorage.getItem("login_token"),
                goodsids: goodsIds
            }).then((data) => {
                let e = JSON.parse(data.data)
                if (e.status == 'ok') {
                    this.$alert(e.msg, '提示', {
                        confirmButtonText: '确定'
                    })
                }

            })
        },
        // 获取商品id
        getGoodsId: function (str) {
            let arr = [];
            arr = str.split('http://');
            arr.shift()
            let goodsIds = '';
            $.each(arr, function (index, val) {
                let num = val.indexOf('=') + 1;
                goodsIds += val.substr(num).replace(/[\r\n]/g,"") + ','
            })
            goodsIds = goodsIds.substring(0,goodsIds.length-1);
            return goodsIds;
        },
        // 清空地址
        clearAddress:function (t) {
            switch (t) {
                case 1:
                this.text = '';
                break;
                case 2:
                this.text2 = '';
                break;
                case 3:
                this.text3 = '';
                break;
                case 4:
                this.text4 = '';
                break;
                case 5:
                this.text5 = '';
                break;
                case 6:
                this.text6 = '';
                break;
                case 7:
                this.text7 = '';
                break;
            }

        },
        // 当前用户所有的店铺
        getShopName:function () {
            this.axios.post(process.env.API_HOST + '/api/thestore/shops', {
                token: sessionStorage.getItem("login_token")
            }).then((data) => {
                let e = JSON.parse(data.data)
                console.log(e)
                if (e.status == 'ok') {
                    this.shopName = e.data
                }
            })
        },
        handleEdit(index, row) {
            console.log(index, row);
        },
        handleDelete(index, row) {
            console.log(index, row);
        },
        //1688运费模板
        basicsMarshop:function () {
            this.axios.get(process.env.API_HOST + '/alibaba/getfreighttemplate').then((data) => {
                let e = data.data
                console.log(e)
                if (e.status == 'ok') {
                    this.Alibaba = e.data.freightTemplates
                    console.log(this.Alibaba)
                } else {
                    this.$alert(e.msg, '错误提示', {
                        confirmButtonText: '确定'
                    })
                }
            })
        },
        // 1688上传
        alichange:function (event){
            console.log(event)
          this.template = event.target.value;
          console.log(this.template)
        },
        alibabagoodsadd: function () {
          let goodsIds = this.getGoodsId(this.text5);
          if (goodsIds == '') {
              this.$alert('请输入商品链接', '错误提示', {
                  confirmButtonText: '确定'
              })
              return;
          }
         this.loading = true;
         this.axios.post(process.env.API_HOST + '/alibaba/productadd', {
              goodsids: goodsIds
          }).then((data) => {
              this.loading = false;
              let e = data.data
              if (e.status == 'ok') {
                  this.$message({
                    message: e.msg,
                    type: 'success'
                  });
              } else {
                  this.$alert(e.msg, '错误提示', {
                      confirmButtonText: '确定'
                  })
              }
          })

        },
        // 1688图片上传
        uploadimg:function () {
            let goodsIds = this.getGoodsId(this.text5);
            if (goodsIds == '') {
                this.$alert('请输入商品链接', '错误提示', {
                    confirmButtonText: '确定'
                })
                return;
            }
            this.loading = true;
            this.axios.post(process.env.API_HOST + '/alibaba/getimagesresource', {
                 goodsids: goodsIds
             }).then((data) => {
                 console.log(data)
                 this.loading = false;
                 let e = data.data
                 if (e.status == 'ok') {
                     this.$message({
                       message: e.msg,
                       type: 'success'
                     });
                 } else {
                     this.$alert(e.msg, '错误提示', {
                         confirmButtonText: '确定'
                     })
                 }
             })

        },
        // 1688获取详情
        getxiangqing:function () {
            if (this.template == '') {
                this.$alert('请选择运费模板', '错误提示', {
                    confirmButtonText: '确定'
                })
                return;
            }
            let goodsIds = this.getGoodsId(this.text5);
            if (goodsIds == '') {
                this.$alert('请输入商品链接', '错误提示', {
                    confirmButtonText: '确定'
                })
                return;
            }
            this.loading = true;
            this.axios.post(process.env.API_HOST + '/alibaba/getproductinfo', {
                 goodsids: goodsIds,
                 template: this.template
             }).then((data) => {
                 this.loading = false;
                 let e = data.data
                 if (e.status == 'ok') {
                     this.$message({
                       message: e.msg,
                       type: 'success'
                     });
                 } else {
                     this.$alert(e.msg, '错误提示', {
                         confirmButtonText: '确定'
                     })
                 }
             })

        },

        // 数据上传 平台选择
        pink1:function(){
            $(".pink").css('display', 'block');
            $('.pink1').css('display', 'none');
            $('.pink2').css('display', 'block');
        },
        pink2:function(){
            $('.pink').css('display', 'none');
            $('.pink1').css('display', 'block');
            $('.pink2').css('display', 'none');
        },
        // auth:function (loginurl){
        //     $('.box_wangzhan').load(loginurl)
        // },
    }
};
</script>
<style src="../../assets/css/index.css" scoped></style>



