<template>
    <div class="">
        <div class="top">
            <span><div>店铺选择</div>
                <select  class="title_option"  @change="freight" v-model="a" @click="pddshop">
                    <option v-for="(item,index) in shopData" :key="index" :value="item.owner_name">{{item.mall_name}}</option>
                </select>
            </span>
            <span><div>运费模板</div>
                <select value="" class="title_option" v-model="b" @change="sendtemplateId">
                    <option v-for="(item,index) in temp" :key="index" :value="item.template_id">{{item.template_name}}</option>
                </select>
            </span>
        </div>
        <div class="middle">
            <div class="box">
                <el-checkbox class="box_left">物流重量
                    <input class="middle_input"/>
                </el-checkbox>
            </div>
            <div class="title">(仅作为辅助上传，不建议批量上传，目前由于某些原因限制，可能会出现错误)</div>
        </div>

        <div class="bottom">
            <div class="size_color">注：第一次使用,点击授权按钮,免费试用15天,15天以后需购买授权使用</div>
            <div class="size_color">关于上传:不可上传拼多多商品,上传价格高于1.08倍,自动按1.08倍上传,突破价格可在上传后用批量改价功能进行改价</div>
            <div class="bottom_open">
                <span>应用订购</span><span>打开授权</span><a>查看注意事项</a>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "pddset",
        data() {
            return {
                owner_name: '',
                shopData: [],
                temp: [],
                a: '',
                b: ''
            }
        },
        // mounted () {
        //     this.pddshop()
        // },
        methods: {
            pddshop:function () {
                this.axios.post(process.env.API_HOST + '/api/thestore/pddshops', {
                    token: sessionStorage.getItem("login_token")
                }).then((data) => {
                    let e = JSON.parse(data.data)
                    if (e.status == 'ok') {
                        this.shopData = e.data
                        //this.a = e.data[0].owner_name
                    }
                })
            },
            freight:function (e) {
                this.$emit('templateId',this.a);
                this.axios.post(process.env.API_HOST + '/api/pnduoduo/getTemplates', {
                    token: sessionStorage.getItem("login_token"),
                    owner_name: this.a
                }).then((data) => {
                    var e = JSON.parse(data.data);
                    if (e.status == 'ok') {
                        if (e.data != '') {
                            //this.b = e.data[0].template_id
                            this.temp = e.data
                        } else {
                            this.b = ''
                            this.temp = []
                        }
                    }
                })
            },
            sendtemplateId: function () {
                this.$emit('appenddata',this.b);
            }
        }
    }
</script>
<style scoped>
    .top, .middle, .bottom {
        margin-left: 5px;
    }
    .title_option {
        width: 120px;
        height: 22px;
    }
    .top span {
        font-size: 12px;
        padding-right: 50px;
    }
    .top span div {
        display: inline-block;
    }
    .middle .title {
        width:416px;
        height:16px;
        font-size: 12px;
        color:rgba(254,81,2,1);
        position: relative;
        top: -47px;
        left: 230px;
        background-color: #FFFFFF;
    }
    .middle .box{
        width: 896px;
        height: 35px;
        border: 1px solid #ccc;
        margin-top: 10px;
        padding-left: 8px;
    }
    .middle .box_left{
        margin-top: 8px;
    }
    .middle .middle_input {
        width: 40px;
        height: 22px;
        border: 1px solid #ccc;
    }
    .bottom .size_color, .bottom .bottom_open a {
        color:rgba(253,0,0,1);
        font-size: 12px;
    }
    .bottom .bottom_open {
        position: relative;
        top: -25px;
        left: 635px;
    }
    .bottom .bottom_open span {
        padding: 3px 12px;
        height: 22px;
        border: 1px solid #1527DC;
        color: #1527DC;
        margin-right: 15px;
    }
</style>



