<template>
    <div>
        <div class="box_pink">
            <el-table
            :data="shopName"
            style="width: 100%">
                <el-table-column
                prop="id"
                label="自增ID"
                width="70">
                </el-table-column>
                <el-table-column
                prop="mall_name"
                label="店铺名称"
                width="80">
                </el-table-column>
                <el-table-column
                prop="shop_type"
                label="平台名称"
                width="80">
                </el-table-column>
                <el-table-column
                prop="owner_name"
                label="店铺账号"
                width="90">
                </el-table-column>
                <el-table-column
                prop="logo"
                label="店铺图片"
                width="80">
                    <template slot-scope="scope">
                        <el-image style="width: 50px; height: 50px" :src="scope.row.logo"></el-image>
                    </template>
                </el-table-column>
                <el-table-column
                prop="mall_desc"
                label="店铺简介"
                width="160">
                </el-table-column>
                <el-table-column
                prop="login_time"
                label="授权时间"
                width="90">
                </el-table-column>
                <el-table-column
                prop="is_token"
                label="是否授权"
                width="80">
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">取消授权</el-button>
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <!-- <div class="box_wangzhan" >
            <iframe id="web" src=""></iframe>
        </div> -->
    </div>

</template>

<script>
    export default {
        name: "shoptable",
        data() {
            return {
                shopName: [],
            }
        },
        created() {
            this.$on('loginurl', data => {
                console.log(data)
            })
        },
        mounted() {
            this.getShopName()
        },
        methods: {
            getShopName:function () {
                this.axios.post(process.env.API_HOST + '/api/thestore/shops', {
                    token: sessionStorage.getItem("login_token")
                }).then((data) => {
                    let e = JSON.parse(data.data)
                    console.log(e)
                    if (e.status == 'ok') {
                        this.shopName = e.data
                    }
                })
            },
            handleEdit(index, row) {
                console.log(index, row);
            },
            handleDelete(index, row) {
                console.log(index, row);
            }

        }
    }
</script>

<style scoped>
    .box_pink {
        background:rgba(238,238,238,1);
        width: 908px;
        height: 425px;
        margin: -8px 10px 0px;
    }
    .box_pink >>> .el-table td, .box_pink >>> .el-table th {
        text-align: center;
        padding: 10px 0;
    }
    .box_pink >>> .el-table td {
        font-size: 12px;
    }
    /* .box_wangzhan {
        height: 268px;
    }
	iframe{
	    width: 908px;
	    height: 277px;
	    margin-top: -8px;
	    border: none;
	} */
</style>



